.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.modal {
  position: relative;
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
  background-color: aliceblue;
}

.btn {
  cursor: pointer;
  position: absolute;
  padding: 8px 16px;

  top: 10px;
  right: 10px;
  font-weight: 700;
  font-size: 14px;
  background: #1e6f9f;
  color: #fff;
  border: none;
  border-radius: 8px;
  text-align: center;
}
